import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import classnames from 'classnames';
import ReactTable from 'react-table';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import Form from 'react-validation/build/form';
import moment from 'moment';
import * as actions from './actions';

class UserManagement extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      data: [],
      oriData: [],
      defaultPageSize: 10,
      isLoading: false,
      searchQuery: '',
      modal: false
    };
    this.formSubmit = this.formSubmit.bind(this);
    this.searchQuery = this.searchQuery.bind(this);
    this.fetchData = this.fetchData.bind(this);
    this.toggleModal = this.toggleModal.bind(this);
    // this.triggerUserActive = this.triggerUserActive.bind(this);
  }
  componentDidMount() {
    this.fetchData();
  }
  formSubmit(event) {
    event.preventDefault();
    const { searchQuery, oriData } = this.state;

    const filteredData = oriData.filter(item => { 
      return (item.username.indexOf(searchQuery) > -1) || (item.email.indexOf(searchQuery) > -1);
    });
    this.setState({ data: filteredData });
  }
  fetchData() {
    const { actions } = this.props;
    this.setState({ isLoading: true });

    actions
      .fetchUserData()
      .then(res => {
        const userList = this.props.reduxUserManagement.find(e => e.status === 200);
        this.setState({
          data: userList.items,
          oriData: userList.items,
          isLoading: false
        });
      });
  }
  searchQuery(e) {
    this.setState({ searchQuery: e.target.value.trim() });
  }
  toggleModal() {
    this.setState({
      modal: !this.state.modal
    });
  }
  triggerUserActive(e, id, active) {
    const { actions } = this.props;
    // actions
    //   .updateUserActive(id, active)
    //   .then(res => {
        const userList = this.state.data;
        const newUserList = userList.map(user => { 
          if (user.id === id) {
            user.active = active;
          }
          return user;
        });
        // console.log(res, id, active);
        this.setState({data: newUserList});        
      // });
  }
  triggerEdit(e, id) {
    this.setState({
      modal: true,
      modalTitle: 'Edit User'
    });
  }
  render() {
    const { data, isLoading, defaultPageSize } = this.state;
    const { authCtrl } = this.props;
    const columns = [
      {
        Header: 'User ID',
        accessor: 'id'
      }, 
      {
        Header: 'Name',
        id: 'username',
        accessor: d => d.username,
        Cell: props => <span>{props.value}</span>
      }, 
      {
        Header: 'Email',
        id: 'email',
        accessor: d => d.email,
        width: 300,
        Cell: props => <span title={props.value}>{props.value}</span>
      }, 
      {
        Header: 'Status',
        id: 'active',
        accessor: d => d.active,
        className: 'text-center',
        Cell: props => <span className={classnames({
          'badge': true,
          'badge-pill': true,
          'badge-info': !!props.value,
          'badge-secondary': !props.value,
        })}><small>{props.value ? 'Active' : 'Inactive'}</small></span>
      }, 
      {
        Header: 'Create Time',
        id: 'confirmed_at',
        minWidth: 140,
        accessor: d => d.confirmed_at,
        className: 'text-center',
        Cell: props => <small title={props.value}>{props.value}</small>
      }, 
      {
        Header: 'Last Login',
        id: 'last_login_at',
        minWidth: 140,
        accessor: d => d.last_login_at,
        className: 'text-center',
        Cell: props => <small title={props.value}>{props.value}</small>
      }, 
      {
        Header: '',
        id: 'controls',
        accessor: d => d,
        width: 280,
        className: 'text-left',
        Cell: props => {
          const mapCtrlList = [
            {
              auth: "user_edit_btn",
              bind: <button key="1" type="button"
                onClick={e => this.triggerEdit.call(this, e, props.value.id)}
                className="btn badge btn-sm btn-outline-primary mr-1 ml-1" >Edit</button>,
            },
            {
              auth: "user_resetpw_btn",
              bind: <button key="2" type="button" className="btn badge btn-sm btn-outline-secondary mr-1 ml-1">Reset Password</button>,
            },
            {
              auth: "user_disable_btn",
              bindKey: props.value.active ? 'disable': 'enable',
              bind: {
                enable: <button key="3" type="button"
                  onClick={e => this.triggerUserActive.call(this, e, props.value.id, true)}
                  className="btn badge btn-sm btn-outline-success mr-1 ml-1">Enable</button>,
                disable: <button key="4" type="button"
                  onClick={e => this.triggerUserActive.call(this, e, props.value.id, false)}
                  className="btn badge btn-sm btn-outline-danger mr-1 ml-1">Disable</button>
              }
            },
            {
              auth: "user_ophistory_btn",
              bind: <button key="5" type="button" className="btn badge btn-sm btn-light mr-1 ml-1">History</button>
            }
          ];

          const showList = () => {
            let allowList = [];
            mapCtrlList.forEach(ctrl => { 
              if (authCtrl.indexOf(ctrl.auth) > -1) {
                if (ctrl.bindKey) {
                  allowList.push(ctrl.bind[ctrl.bindKey]);
                } else {
                  allowList.push(ctrl.bind);
                }
              }
            });
            return allowList;
          }

          return showList();
        }
      }
    ];

    return (
      <div className="">
        <div className="content-box wrap-box--m">
  
          <form className="form-inline mb-4" onSubmit={this.formSubmit}>

            <div className="input-group ml-auto">
              <input className="form-control" type="text" onKeyUp={this.searchQuery} placeholder="Name/Email..." />
              <div className="input-group-append">
                <button className="btn btn-outline-primary" type="submit">Submit</button>
              </div>
            </div>
          </form>
          
          <ReactTable
            columns={columns}
            data={data}
            // sortable={false}
            loading={isLoading}
            defaultPageSize={defaultPageSize}
          />
          
        </div>  
        {/* Modal */}
        <Modal isOpen={this.state.modal} toggle={this.toggleModal} className={this.props.className}>
          <ModalHeader toggle={this.toggleModal}>{this.state.modalTitle}</ModalHeader>
          <ModalBody>
            <Form></Form>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggleModal}>Cancel</Button>{' '}
            <Button color="primary" onClick={this.toggleModal}>Submit</Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => ({
  ...state
});

const mapDispatchProps = (dispatch, ownProps) => ({
  actions: bindActionCreators(actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchProps
)(UserManagement);


