import { FETCH_USER_DATA } from '~/src/reducers/UserManagement/constants';
import * as api from '~/src/api/userManagement';

export const fetchUserData = () => {
  return (dispatch) => {
    return api
      .fetchUserData()
      .then(res => {
        // return promise
        return new Promise((resolve, reject) => {
          if (res.error) {
            reject(res);
          }
          else {
            // dispatch redux action
            dispatch({
              type: FETCH_USER_DATA,
              payload: res
            });
            resolve(res.items);            
          }
        });
      });
  }
}



